"use client";

import { useState } from "react";
import { Modal } from "@/components/ui/modal";
import RichTextEditor from "@/components/ui/editor/RichTextEditor";

/* ================= TYPES ================= */

interface Policy {
    id: string;
    title: string;
    html: string;
}

interface Category {
    id: string;
    name: string;
    description: string;
    policies: Policy[];
}

/* ================= SAMPLE DATA ================= */

const initialCategories: Category[] = [
    {
        id: "leave",
        name: "Leave & Time Off",
        description: "Leave, holidays and attendance policies",
        policies: [
            {
                id: "annual",
                title: "Annual Leave Policy",
                html: "<h2>Annual Leave</h2><p>Employees receive 20 days annually.</p>",
            },
        ],
    },
    {
        id: "conduct",
        name: "Code of Conduct",
        description: "Ethics and professional behavior",
        policies: [],
    },
];

/* ================= PAGE ================= */

export default function HrPoliciesPage() {
    const [categories, setCategories] = useState<Category[]>(initialCategories);
    const [activeCategory, setActiveCategory] = useState<Category | null>(null);
    const [activePolicy, setActivePolicy] = useState<Policy | null>(null);

    const [editorOpen, setEditorOpen] = useState(false);
    const [editingPolicy, setEditingPolicy] = useState<Policy | null>(null);

    /* ========== SAVE POLICY ========== */
    const savePolicy = (data: { title: string; html: string }) => {
        if (!activeCategory) return;

        setCategories((prev) =>
            prev.map((cat) => {
                if (cat.id !== activeCategory.id) return cat;

                // EDIT
                if (editingPolicy) {
                    return {
                        ...cat,
                        policies: cat.policies.map((p) =>
                            p.id === editingPolicy.id
                                ? { ...p, title: data.title, html: data.html }
                                : p
                        ),
                    };
                }

                // CREATE
                return {
                    ...cat,
                    policies: [
                        ...cat.policies,
                        {
                            id: crypto.randomUUID(),
                            title: data.title,
                            html: data.html,
                        },
                    ],
                };
            })
        );

        setEditorOpen(false);
        setEditingPolicy(null);
    };

    /* ========== DELETE POLICY ========== */
    const deletePolicy = () => {
        if (!activeCategory || !activePolicy) return;

        setCategories((prev) =>
            prev.map((cat) =>
                cat.id === activeCategory.id
                    ? {
                        ...cat,
                        policies: cat.policies.filter((p) => p.id !== activePolicy.id),
                    }
                    : cat
            )
        );

        setActivePolicy(null);
    };

    /* ================= UI ================= */

    return (
        <div className="p-6">
            <h1 className="mb-6 text-2xl font-bold">HR Policies</h1>

            {/* ================= CATEGORIES ================= */}
            {!activeCategory && (
                <div className="grid grid-cols-1 gap-6 md:grid-cols-3">
                    {categories.map((cat) => (
                        <div
                            key={cat.id}
                            onClick={() => setActiveCategory(cat)}
                            className="cursor-pointer rounded-xl border p-6 hover:shadow"
                        >
                            <h3 className="text-lg font-semibold">{cat.name}</h3>
                            <p className="text-sm text-gray-500">{cat.description}</p>
                        </div>
                    ))}
                </div>
            )}

            {/* ================= POLICIES ================= */}
            {activeCategory && !activePolicy && (
                <>
                    <div className="mb-4 flex items-center justify-between">
                        <button
                            onClick={() => setActiveCategory(null)}
                            className="text-sm text-blue-600"
                        >
                            ‚Üê Back to Categories
                        </button>

                        <button
                            onClick={() => {
                                setEditingPolicy(null);
                                setEditorOpen(true);
                            }}
                            className="rounded bg-blue-600 px-4 py-2 text-white"
                        >
                            + Create Policy
                        </button>
                    </div>

                    <h2 className="mb-4 text-xl font-semibold">
                        {activeCategory.name}
                    </h2>

                    <div className="grid grid-cols-1 gap-6 md:grid-cols-3">
                        {activeCategory.policies.map((policy) => (
                            <div
                                key={policy.id}
                                onClick={() => setActivePolicy(policy)}
                                className="cursor-pointer rounded-xl border p-6 hover:shadow"
                            >
                                <h4 className="font-semibold">{policy.title}</h4>
                                <p className="text-sm text-gray-500">
                                    Click to view policy
                                </p>
                            </div>
                        ))}
                    </div>
                </>
            )}

            {/* ================= VIEW POLICY ================= */}
            <Modal isOpen={!!activePolicy} onClose={() => setActivePolicy(null)} className="max-w-3xl">
                {activePolicy && (
                    <>
                        <div className="mb-4 flex justify-between">
                            <h2 className="text-xl font-bold">{activePolicy.title}</h2>

                            <div className="flex gap-2">
                                <button
                                    onClick={() => {
                                        setEditingPolicy(activePolicy);
                                        setEditorOpen(true);
                                    }}
                                    className="rounded bg-yellow-500 px-3 py-1 text-white"
                                >
                                    Edit
                                </button>
                                <button
                                    onClick={deletePolicy}
                                    className="rounded bg-red-600 px-3 py-1 text-white"
                                >
                                    Delete
                                </button>
                            </div>
                        </div>

                        {/* HTML rendered exactly */}
                        <div
                            className="prose max-w-none"
                            dangerouslySetInnerHTML={{ __html: activePolicy.html }}
                        />
                    </>
                )}
            </Modal>

            {/* ================= CREATE / EDIT MODAL ================= */}
            <Modal isOpen={editorOpen} onClose={() => setEditorOpen(false)} className="max-w-4xl">
                <PolicyEditor
                    policy={editingPolicy}
                    onSave={savePolicy}
                    onCancel={() => setEditorOpen(false)}
                />
            </Modal>
        </div>
    );
}

/* ================= EDITOR ================= */

function PolicyEditor({
    policy,
    onSave,
    onCancel,
}: {
    policy: Policy | null;
    onSave: (data: { title: string; html: string }) => void;
    onCancel: () => void;
}) {
    const [title, setTitle] = useState(policy?.title || "");
    const [html, setHtml] = useState(policy?.html || "");

    return (
        <div>
            <h2 className="mb-4 text-xl font-bold">
                {policy ? "Edit Policy" : "Create Policy"}
            </h2>

            <input
                className="mb-4 w-full rounded border px-3 py-2"
                placeholder="Policy title"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
            />

            {/* üî• SAME ServiceNow-style editor */}
            <RichTextEditor value={html} onChange={setHtml} />

            <div className="mt-6 flex justify-end gap-2">
                <button onClick={onCancel}>Cancel</button>
                <button
                    onClick={() => onSave({ title, html })}
                    className="rounded bg-blue-600 px-4 py-2 text-white"
                >
                    Save
                </button>
            </div>
        </div>
    );
}
